import React, { useEffect, useState } from "react";
import { Button } from "reactstrap";

export const Repe = ({ id, pNum, pWeigth, edit }) => {
  const [weigth, setWeigth] = useState();
  const [num, setNum] = useState();
  const [isEditable, setIsEditable] = useState();

  useEffect(() => {
    setIsEditable(edit)
    setWeigth(pWeigth)
    setNum(pNum)
  }, [edit, pWeigth, pNum])
  
  return (
    <tr onClick={event => setIsEditable(!isEditable)}>
        <th scope="row">{ id }</th>
        <td>{num}</td>
        <td>{edit ? <input value={weigth} onChange={(event) => setWeigth(event.target.value)}/>: weigth }</td>
        <Button> edit</Button>
    </tr>
  );
};